.modal-menu {
	background-color: $light;
	min-height: 120vh;
	padding-top: 50px;
	padding-bottom: 50px;
	&__content {
		column-count: 4;
		column-gap: 32px;
	}
	&__block {
		margin-bottom: 20px;
		page-break-inside: avoid;
	}
	&__title {
		display: block;
		text-decoration: none;
		margin-bottom: 8px;
		page-break-inside: avoid;
		&:hover {
			opacity: 0.7;
		}
	}
	&__number {
		font-weight: bold;
		font-size: 50px;
		color: $green;
	}
	&__title-text {
		font-size: 20px;
		color: $dark;
	}
	&__link {
		display: block;
		color: #828282;
		text-decoration: none;
		&:hover {
			opacity: 0.7;
		}
	}
}